.App {
  text-align: center;
}
body{
  background: black;
}
.container{
  
  padding: 60px;
  background: #fff;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
table td{
  border: 1px solid grey;
  width: 100px;
  height: 100px;
  font-size: 80px;
}
@media screen and (max-width: 800px) {
  .App {
    text-align: center;
  }
  .container{
    padding: 0px;
    height: 90%;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
  table td{
    border: 1px solid grey;


    font-size: 40px;
  }
  .container h1{
    font-size: 20px;
  }

}


